var actions = ["Acheter du pain", "Réviser le javascript", "Prendre RDV chez le médecin"]; // Initialisation des actions de présentation

///// Class Action CRUD /////
class Action{
    
    // Initialisation des actions
    constructor(){
        
        this.read();
        
    }
    
    // Création d'une action
    create(){
        
        let btn = document.getElementById("addAction");
        let actionToDo = document.getElementById("actionToDo");
        
        btn.addEventListener("click", (e) => {
            
            e.preventDefault();
            
            let result = actionToDo.value;
            
            if(result === ""){ // Si résultat vide
                
                return;
                
            }
            
            actions.push(actionToDo.value); // Ajouter une action à la liste
            this.read(); // Mise à jour de la liste
            actionToDo.value = ""; // Suppression texte input
            
        });
        
    }
    
    // Afficher les actions
    read(){
        
        let action;
        let liste = document.getElementById("listToDo");
        liste.innerHTML = "";
    
        for(action of actions){
        
            liste.innerHTML += `<li>${action} <span class="glyphicon glyphicon-remove"></span></li>`;
        
        }
        
    }
    
    // Mise à jour d'une action
    update(){}
    
    // Supprimer une action
    delete(){
        
       
        
    }
    
}

///// Création du premier objet action /////
let action = new Action();
action.create();

